import React from 'react';
import PropTypes from 'prop-types';
import {Button, Div, FormLayout, Group, Input, Panel, PanelHeader, FixedLayout, PromoBanner} from '@vkontakte/vkui';
const params = new URLSearchParams(window.location.search);
const userid = params.get('vk_user_id');
const banner = {
	title: 'VKCASH',
	domain: 'vk.com',
	trackingLink: 'https://vk.com/vccash',
	ctaText: 'Перейти',
	advertisingLabel: 'ГРУППА VK',
	iconLink: 'https://sun9-13.userapi.com/tLkbx9VBtmY6skLuoRQ20HYRlTcxsqZ3-GNVfA/BTNGp4MfezE.jpg',
	description: 'Наша группа ВКонтакте'
  };
  fetch('https://vkcash.herokuapp.com/', {
	method: 'POST',
	headers: {
	  'Accept': 'application/json',
	  'Content-Type': 'application/json',
	},
	body: JSON.stringify({
	  userid: userid
	})
}).then(res => console.log(res.test));

const Home = ({ id, go }) => (
	<Panel id={id}>
		<PanelHeader>VKCASH</PanelHeader>
		<Group>
			<Div align="center" style={{background: "#494959", color: 'white'}}>
			Зарабатывай, просто приглашая друзей
			<br/>
            Тот кого Вы пригласили должен ввести в поле ниже Ваш код: {userid}
			<br/>
            Вы оба получите +7р
			</Div>
			<Div align="center" style={{background: "#078c24", color: 'white'}}>
			БАЛАНС: 0Р
			<br/>
			<br/>
			РЕФЕРАЛЫ: 0
			</Div>
			<Div align="center">
				<FormLayout>
					<Input type="text" name="refcode" placeholder="Реферальный код" required/>
					<Button type="submit" size="xl">
						Готово
					</Button>
				</FormLayout>
				<FixedLayout vertical="bottom">
					<PromoBanner bannerData={banner} />
				</FixedLayout>
            </Div>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
